import { MoreHorizOutlined } from '@mui/icons-material';
import {
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Menu,
  MenuItem,
  Typography,
} from '@mui/material';
import { map, omit } from 'lodash-es';
import React from 'react';
import Popper from '../../builtIn/components/Popper';
import { useState } from 'react';
import FormWidget from './form';
import { useRef } from 'react';
import { useMemo } from 'react';
import { generateShortId, runAsync } from '../../misc';
import { useShallowMemo } from '../../hooks/common';
import { useCallback } from 'react';
import { Box } from '@mui/material';
import { Add } from '@mui/icons-material';

const ListWidget = (props) => {
  const { value, onChange, fields, mainField, initValue, style = {} } = props;

  const identifiedValue = useShallowMemo(() => {
    return value.map((i) => {
      if (i.id) {
        return i;
      }
      return { ...i, id: generateShortId() };
    });
  }, [value]);

  const [popperOpen, setPopperOpen] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [expand, setExpand] = useState('');
  const popperAnchorRef = useRef<HTMLElement>(null);
  const menuAnchorRef = useRef<HTMLElement>(null);
  const listRef = useRef<HTMLLIElement>(null);

  const onAdd = useCallback(() => {
    let val = initValue[mainField];
    let inc = 1;
    if (val.endsWith('${inc}')) {
      const pureVal = val.replace('${inc}', '');
      val = pureVal + inc;
      while (identifiedValue.find((i) => i[mainField] === val)) {
        val = pureVal + ++inc;
      }
    }
    identifiedValue.push({ ...initValue, [mainField]: val });
    onChange([...identifiedValue]);
    runAsync(() => {
      listRef.current?.scrollTo({
        top: listRef.current.scrollHeight - listRef.current.offsetHeight,
      });
    });
  }, [onChange, identifiedValue, initValue]);

  return (
    <Box
      sx={{
        position: 'relative',
      }}
    >
      <IconButton
        size="small"
        onClick={onAdd}
        sx={{
          position: 'relative',
          left: 'calc(100% - 20px)',
        }}
      >
        <Add
          sx={{
            fontSize: '12px',
          }}
        />
      </IconButton>
      <List
        disablePadding
        ref={listRef}
        sx={{
          border: '1px solid var(--border-1)',
          fontSize: '12px',
          borderRadius: '3px',
          maxHeight: '200px',
          overflow: 'auto',
          ...style,
        }}
      >
        {!identifiedValue.length && (
          <ListItem
            disablePadding
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              height: '26px',
            }}
            onClick={onAdd}
          >
            <Typography
              color="textSecondary"
              fontSize={12}
              textAlign={'center'}
            >
              请添加选项
            </Typography>
          </ListItem>
        )}
        {map(identifiedValue, (item) => {
          return (
            <ListItem
              disableGutters
              disablePadding
              key={item.id}
              secondaryAction={
                <ListItemButton
                  onClick={(e) => {
                    setMenuOpen(item.id);
                    menuAnchorRef.current = e.currentTarget;
                  }}
                  sx={{
                    fontSize: '12px',
                    padding: '2px',
                    borderRadius: '50%',
                    paddingRight: '4px',
                  }}
                >
                  <MoreHorizOutlined fontSize="inherit" />
                </ListItemButton>
              }
            >
              <ListItemButton
                onClick={(e) => {
                  setPopperOpen(true);
                  setExpand(item.id);
                  popperAnchorRef.current = e.currentTarget;
                }}
                sx={{
                  padding: '4px 6px',
                  bgcolor:
                    item.id === expand
                      ? 'var(--container-bg-hover-1) !important'
                      : '',
                }}
              >
                <ListItemText
                  sx={{
                    m: '0',
                    '&>.MuiTypography-root': {
                      fontSize: '12px',
                    },
                  }}
                >
                  {item[mainField]}
                </ListItemText>
              </ListItemButton>
            </ListItem>
          );
        })}
      </List>
      {expand && (
        <Popper
          title="选项配置"
          open={popperOpen}
          onOpenChange={setPopperOpen}
          anchorEl={popperAnchorRef.current}
          offset={[-200, 0]}
        >
          <FormWidget
            fields={fields}
            key={expand}
            value={identifiedValue.find((item) => item.id === expand)}
            onChange={(option) => {
              const idx = identifiedValue.findIndex(
                (item) => item.id === option.id,
              );
              const newValue = [...identifiedValue];
              newValue.splice(idx, 1, option);
              onChange(newValue);
            }}
          />
        </Popper>
      )}
      <Menu
        open={!!menuOpen}
        anchorEl={menuAnchorRef.current}
        onClose={() => setMenuOpen(false)}
      >
        <MenuItem
          sx={{
            fontSize: '12px',
            color: 'red',
          }}
          onClick={() => {
            const newValue = identifiedValue.filter((i) => i.id !== menuOpen);
            onChange(newValue);
            setMenuOpen(false);
            setExpand('');
          }}
        >
          删除
        </MenuItem>
      </Menu>
    </Box>
  );
};

export default ListWidget;
