import React from 'react';
import useCanvasStore from '../canvas/store';
import styles from './index.scss';
import { useSchemaStore } from '../schema';
import useComponentStore from '../components/store';
import widgets from './widgets';
import { useStatesStore } from '../states';
import { useCallback } from 'react';
import EventList, { getDefaultEvent } from './eventList';
import {
  Breadcrumbs,
  Typography,
  Link,
  Box,
  Divider,
  IconButton,
} from '@mui/material';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import { useActionStore } from '../action';
import { useRef } from 'react';
import EmptySvg from '../assets/images/no-data.svg';
import Tooltip from '../builtIn/components/Tooltip';
import { Close as CloseIcon } from '@mui/icons-material';
import { useLayoutStore } from '../editor';
import { transformDataType } from '../misc';
import { useShallow } from 'zustand/react/shallow';
import { LegendToggle } from '@mui/icons-material';
import { Code } from '@mui/icons-material';
import { useMemo } from 'react';
import { useState } from 'react';
import { BackHand } from '@mui/icons-material';
import { SettingsBackupRestore } from '@mui/icons-material';
import { groupBy, sortBy } from 'lodash-es';

const PropsPanelHeader = () => {
  const selected = useCanvasStore((state) => state.selected);
  const setPropsPanelVisible = useLayoutStore(
    (state) => state.setPropsPanelVisible,
  );

  return (
    <Box
      display={'flex'}
      alignItems={'center'}
      paddingLeft={'10px'}
      height={25}
    >
      <Typography fontWeight={1000} fontSize={13} flex={1}>
        {selected}
      </Typography>
      <CloseIcon
        sx={{ fontSize: 13, mr: 1, cursor: 'pointer' }}
        onClick={() => setPropsPanelVisible(false)}
      />
    </Box>
  );
};

const PropsPanelItem = (props) => {
  const {
    widget: widgetType,
    _key: key,
    name,
    schemaNode,
    widgetProps,
    accept,
    hidden,
  } = props;
  const { id } = schemaNode;
  let Widget = widgets[widgetType] || (() => 'not support yet');
  const stateValue = useSchemaStore(
    (state) => state.plainSchema[id].state[key],
  );
  const setState = useStatesStore((state) => state.setState);
  const updateNodePrecisely = useSchemaStore(
    (state) => state.updateNodePrecisely,
  );
  const states = useStatesStore(
    useShallow((state) => ({ ...state[schemaNode.id] })),
  );

  const onChange = useCallback(
    (value) => {
      const acceptableValue = transformDataType(value, accept);
      setState(id, { [key]: acceptableValue });
      updateNodePrecisely(id, ['state', key], acceptableValue);
    },
    [setState, key, updateNodePrecisely, id, accept],
  );

  const actualWidgetProps = {
    ...widgetProps,
  };
  if (widgetType !== 'editor' && widgetType !== 'input') {
    // 防止浏览器告警
    delete actualWidgetProps.tipProps;
  }
  const [hovered, setHovered] = useState(false);
  const showWidgetConvert = useMemo(() => {
    return widgetType !== 'editor' && hovered;
  }, [widgetType, hovered]);

  // 优先用 schema 中记录的部件
  const schemaWidget = useSchemaStore(
    (state) => state.plainSchema[id].widgets?.[key],
  );
  Widget = widgets[schemaWidget] || Widget;

  let _hidden = hidden;
  if (typeof hidden === 'function') {
    _hidden = hidden(states);
  }

  if (_hidden) {
    return null;
  }

  return (
    <div
      className={styles['props-panel-item']}
      onKeyDown={(e) => e.stopPropagation()}
      onMouseOver={() => setHovered(true)}
      onMouseOut={() => setHovered(false)}
    >
      <Tooltip title={key} placement="left-end" arrow>
        <div className={styles['props-panel-item-label']}>
          <Typography fontSize={12}>{name}</Typography>
        </div>
      </Tooltip>
      {showWidgetConvert && (
        <IconButton
          size="small"
          sx={{
            fontSize: 12,
            position: 'absolute',
            left: '45px',
          }}
          onClick={() => {
            useSchemaStore.getState().updateNode(id, {
              widgets: {
                [key]: schemaWidget ? '' : 'editor',
              },
            });
          }}
        >
          {schemaWidget ? (
            <SettingsBackupRestore fontSize="inherit" />
          ) : (
            <Code fontSize="inherit" />
          )}
        </IconButton>
      )}
      <div className={styles['props-panel-item-widget']}>
        <Widget value={stateValue} onChange={onChange} {...actualWidgetProps} />
      </div>
    </div>
  );
};

const EmptyContent = (props) => {
  return (
    <Box
      display="flex"
      flexDirection="column"
      alignItems="center"
      justifyContent="center"
      rowGap="20px"
      height="300px"
    >
      <EmptySvg width={120} preserveAspectRatio="xMidYMid meet" />
      <Typography variant="caption">未选择组件</Typography>
    </Box>
  );
};

function renderItems(attributes, schemaNode) {
  return (
    <div className={styles['items-container']}>
      {attributes.map((attri) => {
        const attriCopy = { ...attri, _key: attri.key };
        return <PropsPanelItem {...attriCopy} schemaNode={schemaNode} />;
      })}
    </div>
  );
}

const PanelSection = (props) => {
  const { children, title, onAdd } = props;

  return (
    <div className={styles['panel-section']}>
      <div className={styles['panel-section-title']}>
        <Typography fontSize={13} fontWeight={500}>
          {title}
        </Typography>
        {onAdd && (
          <span className={styles['panel-section-title-add']} onClick={onAdd}>
            <AddOutlinedIcon fontSize="inherit" />
          </span>
        )}
      </div>
      <div className={styles['panel-section-content']}>{children}</div>
    </div>
  );
};

const categoryMap = {
  content: {
    name: '内容',
    order: 0,
  },
  interaction: {
    name: '交互',
    order: 5,
  },
  layout: {
    name: '布局',
    order: 10,
  },
  spacing: {
    name: '间距',
    order: 15,
  },
  appearance: {
    name: '外观',
    order: 20,
  },
  other: {
    name: '其他',
    order: 999,
  },
};

const PropsPanel = () => {
  const selectedId = useCanvasStore((state) => state.selected);
  const schemaNode = useSchemaStore((state) => state.plainSchema[selectedId]);

  const componentInfo = useComponentStore((state) => state[schemaNode?.type]);

  const componentSchema = componentInfo?.schema;

  const attributes = componentSchema?.attributes?.sort(
    (a, b) => categoryMap[a.category]?.order - categoryMap[b.category]?.order,
  );

  const groupedAttris = groupBy(attributes, 'category');

  groupedAttris.other = groupedAttris.undefined;
  delete groupedAttris.undefined;

  const showEventList = !!componentSchema?.events?.length;

  const addEvent = useActionStore((state) => state.addEvent);
  const getLatestEvent = useActionStore((state) => state.getLatestEvent);

  const eventListRef = useRef<any>(null);

  return (
    <div className={styles['props-panel']}>
      <div className={styles['props-panel-inner']}>
        <PropsPanelHeader />
        <Divider />
        {selectedId && componentInfo ? (
          <>
            {Object.entries(groupedAttris).map(([category, attris]) => {
              if (!attris?.length) {
                return null;
              }
              return (
                <PanelSection
                  key={category}
                  title={categoryMap[category]?.name}
                >
                  {renderItems(attris, schemaNode)}
                </PanelSection>
              );
            })}
            {showEventList && (
              <>
                <PanelSection
                  key="event"
                  title="事件"
                  onAdd={() => {
                    addEvent(selectedId, getDefaultEvent(schemaNode.type));
                    const theEvent = getLatestEvent(selectedId);
                    setTimeout(() => {
                      eventListRef.current?.openEventPanel(theEvent.id);
                    });
                  }}
                >
                  <EventList ref={eventListRef} />
                </PanelSection>
              </>
            )}
          </>
        ) : (
          <EmptyContent />
        )}
      </div>
    </div>
  );
};

export default PropsPanel;
