import CodeMirror, { Decoration, RangeSetBuilder } from '@uiw/react-codemirror';
import { javascript } from '@codemirror/lang-javascript';
import React, { CSSProperties, useCallback, useMemo, useRef } from 'react';
import { EditorView, keymap } from '@codemirror/view';
import useGlobalStore from '../../../globalStore';
import { startCompletion } from '@codemirror/autocomplete';
import { compileExpression } from '../../../expression';
import { forEach, isEmpty } from 'lodash-es';
import { mustacheAutocomplete, mustacheCompletion } from '../../../codemirror';
import styles from './index.scss';
import { runAsync } from '../../../misc';
import Popper from '../Popper';
import { useState } from 'react';
import CodeEditorTip, { IProps as CodeEditorTipProps } from './CodeEditorTip';
import { javascriptLanguage } from '@codemirror/lang-javascript';
styles;

const customKeyMap = keymap.of([
  {
    key: 'Mod-Shift-i',
    mac: 'Cmd-Shift-i',
    run: (view) => {
      startCompletion(view);
      return true;
    },
    stopPropagation: true,
    preventDefault: true,
  },
]);

// 定义 {{ }} 区域的装饰器
const mustacheHighlight = Decoration.mark({
  class: 'cm-mustache',
  attributes: { title: 'Mustache template' },
});

const mustacheErrorHighlight = Decoration.mark({
  class: 'cm-mustache-error',
  attributes: { title: 'Mustche template error' },
});

// 创建装饰扩展
const mustacheDecoration = EditorView.decorations.compute(['doc'], (state) => {
  const builder = new RangeSetBuilder();
  const str = state.doc.toString();
  const expressionRegExp = /\{\{([\s\S]*?)\}\}/;
  if (!expressionRegExp.test(str)) {
    return builder.finish();
  }
  const [returnValue, deps, records] = compileExpression(str);

  forEach((records as any[]).reverse(), (record) => {
    const from = record.index;
    const to = record.index + record.expressionWithBracket.length;
    builder.add(
      from,
      to,
      record.err ? mustacheErrorHighlight : mustacheHighlight,
    );
  });

  return builder.finish() as any;
});

interface IProps {
  value: string;
  onChange: (e: any, value: string) => void;
  id?: string;
  name?: string;
  style?: CSSProperties;
  className?: string;
  hideLineNo?: boolean;
  tipProps?: CodeEditorTipProps;
  lineWrapping?: boolean;
}

function CodeEditor(props: IProps) {
  const {
    id,
    value,
    onChange,
    name,
    style,
    hideLineNo = false,
    tipProps,
    lineWrapping = true,
  } = props;

  const handleChange = useCallback(
    (val, viewUpdate) => {
      const event = {
        target: {
          name,
          value: val,
        },
      };
      onChange(event, val);
      // startCompletion(viewUpdate.view);
    },
    [onChange, name],
  );

  const handleFocus = useCallback((e) => {
    runAsync(() => {
      startCompletion(EditorView.findFromDOM(e.target)!);
    });
    setTipOpen(true);
  }, []);

  const handleBlur = useCallback((e) => {
    setTipOpen(false);
  }, []);

  const [tipOpen, setTipOpen] = useState(false);
  const editorBottomRef = useRef<HTMLDivElement>(null);

  const customTheme = useMemo(() => {
    const css: Record<string, CSSProperties> = {
      '&': {
        fontSize: '12px', // 基础字体大小
        ...style,
      },
      '.cm-content': {
        fontFamily: "'JetBrains Mono', monospace",
        fontSize: '11px', // 内容区字体大小
        lineHeight: '18px',
      },
      '.cm-gutters': {
        fontSize: '8px', // 行号区字体大小
        display: hideLineNo ? 'none' : '',
      },
      '.cm-mustache': {
        backgroundColor: 'rgba(186,239,205, .4)',
        borderRadius: 2,
        padding: '0 2px',
      },
      '.cm-mustache-error': {
        backgroundColor: 'rgba(250,219,222, .4)',
        borderRadius: 2,
        padding: '0 2px',
      },
      '.cm-activeLine.cm-line': {
        backgroundColor: 'transparent',
      },
    };

    return EditorView.theme(css as any);
  }, [hideLineNo, style]);

  const platformTheme = useGlobalStore((state) => state.theme);

  const showTip = useMemo(() => {
    return !isEmpty(tipProps);
  }, [tipProps]);

  const extensions = useMemo(() => {
    const ext = [
      javascript({ jsx: true }),
      customTheme,
      mustacheAutocomplete,
      mustacheDecoration,
      customKeyMap,
    ];
    if (lineWrapping) {
      ext.push(EditorView.lineWrapping);
    }
    return ext;
  }, [lineWrapping]);

  return (
    <div className={styles['code-editor-container']}>
      <CodeMirror
        id={id}
        value={value}
        extensions={extensions}
        onChange={handleChange}
        onFocus={handleFocus}
        onBlur={handleBlur}
        theme={platformTheme}
      />
      <div ref={editorBottomRef as any} />
      {showTip && (
        <Popper
          open={tipOpen}
          onOpenChange={(open) => setTipOpen(open)}
          anchorEl={editorBottomRef.current!}
          showClose={false}
          offset={[0, 5]}
          width={editorBottomRef.current?.offsetWidth}
          style={{
            borderRadius: '3px',
            backgroundColor: 'transparent',
          }}
          disablePadding
        >
          <CodeEditorTip
            {...tipProps}
            onMouseDown={(e) => {
              e.preventDefault();
            }}
          />
        </Popper>
      )}
    </div>
  );
}

export default CodeEditor;
