import { Box, Typography } from '@mui/material';
import { debounce, get, map } from 'lodash-es';
import React from 'react';
import widgets from '.';
import { useRef } from 'react';
import { useCallback } from 'react';
import { set } from 'lodash-es';
import { useReducer } from 'react';

const FormWidget = (props) => {
  const { fields, value, onChange } = props;

  const debouncedOnChange = useCallback(debounce(onChange), [onChange]);
  const [_, forceUpdate] = useReducer((x) => x + 1, 0);

  const formRef = useRef({
    initialValues: { ...value },
    values: { ...value },
    setValue: (field, value) => {
      set(formRef.current.values, field, value);
      forceUpdate();
      debouncedOnChange(formRef.current.values);
    },
  });

  const handleSubChange = useCallback((field, value) => {
    formRef.current.setValue(field, value);
  }, []);

  return (
    <Box width={200} display={'flex'} flexDirection={'column'} rowGap={'5px'}>
      {map(fields, (fieldItem) => {
        const { field, widget, label } = fieldItem;
        const Widget = widgets[widget];
        return (
          <Box key={field}>
            <Typography fontSize={12}>{label}</Typography>
            <Widget
              value={get(formRef.current.values, field)}
              onChange={(val) => handleSubChange(field, val)}
            />
          </Box>
        );
      })}
    </Box>
  );
};

export default FormWidget;
