import React from 'react';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { useShallowMemo } from '../../hooks/common';
import { useCallback } from 'react';
import { ListItemIcon, ListItemText } from '@mui/material';
import { Typography } from '@mui/material';
import { Box } from '@mui/material';

const SelectWidget = (props) => {
  const {
    value,
    onChange,
    options = [],
    showOptionLabel = true,
    ...remain
  } = props;

  const menuItems = useShallowMemo(
    () =>
      options.map((item) => (
        <MenuItem key={item.value} value={item.value}>
          <Box
            sx={{
              width: '100%',
              display: 'flex',
              flexWrap: 'nowrap',
              alignItems: 'center',
            }}
          >
            <Typography sx={{ font: 'inherit', flex: 1 }}>
              {item.label}
            </Typography>
            {showOptionLabel && (
              <Typography color="textDisabled" sx={{ fontSize: 11 }}>
                {item.value}
              </Typography>
            )}
          </Box>
        </MenuItem>
      )),
    [options, showOptionLabel],
  );

  const onValueChange = useCallback(
    (e) => onChange(e.target.value),
    [onChange],
  );

  return (
    <Select
      value={value}
      onChange={onValueChange}
      style={{ height: '30px', fontSize: '11px' }}
      fullWidth
      {...remain}
      size="small"
      renderValue={(value) => options.find((opt) => opt.value === value).label}
      MenuProps={{
        sx: {
          '& .MuiMenuItem-root': {
            fontSize: '11px',
          },
        },
      }}
    >
      {menuItems}
    </Select>
  );
};

export default SelectWidget;
