import React from 'react';
import CodeEditor from '../../builtIn/components/CodeEditor';

const EditorWidget = (props) => {
  const { value, onChange, ...remain } = props;

  return (
    <CodeEditor
      value={String(value)}
      onChange={(e, val) => onChange(val)}
      {...remain}
      hideLineNo
      style={{
        maxHeight: '300px',
        overflow: 'auto',
      }}
    />
  );
};

export default EditorWidget;
